<template>
  <base-layout
    :is-header-showed="!$vuetify.breakpoint.mobile"
    :white-background="$vuetify.breakpoint.mobile"
  >
    <template #header>
      <div class="d-flex justify-space-between align-center full-width">
        <div class="d-flex">
          <v-btn
            text
            depressed
            rounded
            color="transparent"
            min-width="auto"
            height="auto"
            class="px-0"
            :to="{ name: $constants.ROUTE_NAMES.DRIVERS }"
          >
            <v-img
              class="mr-4"
              alt="Back"
              :max-width="18"
              :src="require('@images/icons/drivers/back.svg')"
            />
          </v-btn>

          <h1 class="font-weight-bold primary-dark--text">
            {{ $route.meta.pageTitle }}
          </h1>
        </div>
      </div>
    </template>
    <template #content>
      <div class="content-page-wrapper d-flex flex-column flex-md-row flex-shrink-1 px-lg-6 py-lg-8 px-0 my-md-0 mx-lg-n2 mx-md-auto">
        <v-card
          width="100%"
          :height="$vuetify.breakpoint.mobile ? '100%' : 'fit-content'"
          class="user-card py-lg-5 py-md-12 pt-12 pb-7 mx-lg-2 mx-md-2 mx-5 px-lg-5 px-sm-12"
          :flat="$vuetify.breakpoint.mobile"
        >
          <v-form
            ref="form"
            :class="[
              $vuetify.breakpoint.mobile ? 'd-flex flex-column justify-space-between' : '',
              'form-wrapper full-height'
            ]"
            v-model="form.valid"
            lazy-validation
            @keyup.enter.native="onSubmit"
          >
            <div>
              <base-field-input
                label="Full Name"
                          :is-required="true"
                v-model="form.values.full_name"
                :rules="form.fullNameRules"
              />

              <base-field-input
                v-model.trim="form.values.email"
                label="Email"
                          :is-required="true"
                :rules="form.emailRules"
                :error="Boolean((serverErrors && serverErrors.email) || serverError)"
                :error-messages="serverErrors.email"
                validate-on-blur
              />

              <base-field-input
                v-model="form.values.phone"
                mask="+1 (###) ###-####"
                label="Phone"
                          :is-required="true"
                :rules="form.phoneRules"
              />
            </div>

            <v-btn
              :width="$vuetify.breakpoint.mobile ? '100%' : 100"
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              depressed
              color="primary"
              class="mt-2"
              :class="{ 'font-weight-bold mobile-fixed-buttons mobile-fullsize-button': $vuetify.breakpoint.mobile }"
              :disabled="!form.valid || drivers.loading"
              :loading="drivers.loading"
              @click="onSubmit"
            >
              Invite
            </v-btn>
          </v-form>
        </v-card>
      </div>
    </template>
  </base-layout>
</template>

<script>
import { identity } from 'ramda';
import { createNamespacedHelpers } from 'vuex';

import BaseLayout from '@components/BaseLayout';
import BaseFieldInput from '@components/BaseFieldInput';

import {
  required,
  email,
} from '@utils/helpers/validationRules';

import * as constants from '@modules/drivers/constants';

const { mapActions, mapState } = createNamespacedHelpers('drivers');

export default {
  name: 'DriverInvite',
  components: {
    BaseLayout,
    BaseFieldInput,
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data: () => ({
    form: {
      valid: false,
      values: {
        full_name: '',
        email: '',
        phone: '',
      },
      fullNameRules: [required('Full Name')],
      emailRules: [required('Email'), email],
      phoneRules: [required('Phone')],
    },
    serverError: '',
    serverErrors: {},
  }),
  computed: {
    ...mapState(['drivers']),
  },
  methods: {
    onSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;

      this.inviteDriver({ data: this.form.values })
        .then(() => {
          this.$notificationSuccess('Driver is invited');
          this.$router.push({ name: this.$constants.ROUTE_NAMES.DRIVERS }).catch(identity);
        })
        .catch((errors) => {
          this.$notificationError('Driver wasn\'t invited');
          if (Array.isArray(errors)) {
            this.serverError = errors;
          } else {
            this.serverErrors = errors;
          }
        });
    },
    ...mapActions({
      inviteDriver: constants.actions.INVITE_DRIVER,
    }),
  },
};
</script>
